import * as React from 'react';

export default class Form extends React.Component {

  render() {
    return (
      <div className="form">
        <div className="container narrow-desktop-grid">
          <div className="row">
            <div className="col">
              <div className="form-group">
                <label className="text-standard" htmlFor="">Textflt / Textflt i fokus</label>
                <input type="text" className="form-control" id="" placeholder="Platshllare" />
              </div>
              <div className="form-group">
                <label className="text-standard margin-reset">Textflt med beskrivning</label>
                <div className="form-text text-standard">Beskrivning av textflt</div>
                <input type="text" className="form-control" id="" placeholder="Platshllare" />
              </div>
              <div className="form-group">
                <label className="text-standard">Textflt ej valbar</label>
                <input type="text" className="form-control"
                  id="" placeholder="" readOnly={true} />
              </div>
              <div className="form-group">
                <label className="text-standard">Textflt med validering<span className="text-error">*</span></label>
                <input type="text" className="form-control form-control--error icon-warning"
                  id="" placeholder="" />
                <div className="text-standard text-error">Hjlpsamt felmeddelande</div>
              </div>
            </div>
          </div>
          <div className="row spacer-mt-xlarge">
            <div className="col">
              <div className="form-group">
                <label className="text-standard" htmlFor="">Textomrde / Textomrde i fokus</label>
                <textarea className="form-control" id="" rows={4} placeholder="Platshllare" />
              </div>
              <div className="form-group">
                <label className="text-standard margin-reset">Textomrde med beskrivning</label>
                <div className="form-text text-standard">Beskrivning av textomrde</div>
                <textarea className="form-control" id="" rows={4} placeholder="Platshllare" />
              </div>
              <div className="form-group">
                <label className="text-standard">Textomrde  ej valbar</label>
                <textarea className="form-control" id="" rows={4} readOnly={true} />
              </div>
              <div className="form-group">
                <label className="text-standard">Textomrde med validering<span className="text-error">*</span>
                </label>
                <textarea className="form-control form-control--error form-control--error-textarea icon-warning"
                  id="" rows={4} />
                <div className="text-standard text-error">Hjlpsamt felmeddelande</div>
              </div>
            </div>
          </div>
          <div className="row spacer-mt-xlarge">
            <div className="col spacer-mb-large">
              <div className="spacer-mb-medium">
                <label className="text-standard" htmlFor="">Enval</label>
                <div className="custom-control custom-control--radio">
                  <input type="radio" id="customRadio1" name="customRadio" className="custom-control__input" />
                  <label className="custom-control__label" htmlFor="customRadio1">Radio 1</label>
                </div>
                <div className="custom-control custom-control--radio">
                  <input type="radio" id="customRadio2" name="customRadio" className="custom-control__input" />
                  <label className="custom-control__label" htmlFor="customRadio2">Radio 2</label>
                </div>
                <div className="custom-control custom-control--radio">
                  <input type="radio" className="custom-control__input" name="disabledRadio"
                    id="disabledRadio" disabled={true} />
                  <label className="custom-control__label" htmlFor="disabledRadio">Ej valbar</label>
                </div>
              </div>
              <div className="spacer-mb-large">
                <label className="text-standard" htmlFor="">Flerval</label>
                <div className="custom-control custom-control--checkbox">
                  <input type="checkbox" className="custom-control__input" id="customCheck1" />
                  <label className="custom-control__label" htmlFor="customCheck1">Alternativ</label>
                </div>
                <div className="custom-control custom-control--checkbox">
                  <input type="checkbox" className="custom-control__input" id="customCheck2" disabled={true} />
                  <label className="custom-control__label" htmlFor="customCheck2">Ej valbar</label>
                </div>
              </div>
              <div className="form-group">
                <label className="text-standard" htmlFor="">Rullgardin</label>
                <select className="form-control dropdown icon-dropdown-arrow" id="">
                  <option value="1">Platshllare</option>
                </select>
              </div>
              <div className="spacer-mb-xxlarge">
                <div className="form-group">
                  <label className="text-standard" htmlFor="">Rullgardin ej valbar</label>
                  <select className="form-control dropdown icon-dropdown-arrow-disabled"
                    id="" disabled={true}>
                    <option value="1">Platshllare</option>
                  </select>
                </div>
              </div>
              <div className="form-group">
                <label className="text-standard" htmlFor="">Skflt / textflt med ikon</label>
                <div className="display-flex">
                  <input type="text" className="form-control form-control--search icon-search"
                    id="" placeholder="Platshllare" />
                  <button className="btn btn--primary">Knapp normal</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}



