import * as React from 'react';

export default class Feedback extends React.Component {
  render() {
    return (
      <div className="narrow-desktop-grid">
        <div className="spacer-mb-xxlarge">
          <div className="feedback">
            <div className="feedback__box">
              <div className="container">
                <div className="row">
                  <div className="col-12 col-md-6">
                    <div className="feedback__box-text">
                      <span className="text-standard">Hjlpte informationen p den hr sidan dig?</span>
                    </div>
                  </div>
                  <div className="col-12 col-md-6">
                    <div className="feedback__box-buttons">
                      <div className="feedback__box-buttons--first">
                        <button className="btn btn--primary btn--small btn--block">Ja</button>
                      </div>
                      <div className="feedback__box-buttons--second">
                        <button className="btn btn--primary btn--small btn--block">Nej</button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="spacer-mb-xxlarge">
          <div className="feedback">
            <div className="feedback__box">
              <div className="container">
                <div className="row">
                  <div className="col">
                    <div className="feedback__box-text-only">
                      <span className="text-standard">Tack fr din feedback!</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="spacer-mb-xxlarge">
          <div className="feedback">
            <div className="feedback__box">
              <div className="container">
                <div className="row">
                  <div className="col">
                    <div className="feedback__box-text-area">
                      <div className="feedback__box-text-area--title text-standard">
                        <strong>Tack, bertta grna hur vi kan frbttra den hr sidan</strong>
                      </div>
                      <div className="form-group">
                        <textarea className="form-control" id="" rows={4} placeholder="Platshllare" />
                      </div>
                      <button className="btn btn--primary btn--small">Skicka kommentar</button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}



